import React, { useState, useEffect } from "react";
import { Button } from "primereact/button";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import PostWriteModal from "./Modal/post-write";
import $ from "jquery";
import "./Home.scss";

function Home({ isUserState, tokenUtill }) {
    const [customers, setCustomers] = useState([]);
    const [PWModal, setPWModal] = useState(false);
    const showPostWriteModal = () => {
        setPWModal(true);
    };


    const paginatorLeft = <Button type="button" icon="pi pi-refresh" text />;

    async function loadPostList() {
        let headers;
        let accessToken = await tokenUtill.getAccessToken();

        if (await accessToken) {
            headers = { "Content-Type": "application/json", authorization: "Bearer " + (await accessToken.signature) };
            console.log(headers);
        }
        let query = "mutation{ PostList { id title desc files authority date userName}}";
        let result = await tokenUtill.simpleGraphqlAjax(query, headers);
        setCustomers(result?.PostList);
    }

    const header = (
        <div className="post-table-header">
            {" "}
            <Button type="button" onClick={showPostWriteModal} raised>
                 
            </Button>
        </div>
    );

   

    useEffect(() => {
        loadPostList();
    }, [isUserState]);
    function postModify(e) {
        console.log($(e.target).attr("data-postId"));
    }

    function postdelete(e) {
        console.log($(e.target).attr("data-postId"));
    }
    const bodytem = function (rowData) {
        console.log(rowData.authority);
        console.log(rowData);
        if (rowData.authority) {
            return (
                <>
                    <Button severity="warning" data-postId={rowData.id} onClick={postModify} className="post-modify-bnt" raised>
                        
                    </Button>
                    <Button severity="danger" data-postId={rowData.id} onClick={postdelete} className="post-delete-bnt" raised>
                        
                    </Button>
                </>
            );
        } else {
            return "";
        }
    };
    return (
        <>
            <PostWriteModal PWModal={PWModal} setPWModal={setPWModal} tokenUtill={tokenUtill}></PostWriteModal>
            <div className="card">
                <DataTable
                    header={header}
                    value={customers}
                    paginator
                    rows={5}
                    rowsPerPageOptions={[5, 10, 25, 50]}
                    tableStyle={{ minWidth: "50rem" }}
                    paginatorTemplate="RowsPerPageDropdown FirstPageLink PrevPageLink CurrentPageReport NextPageLink LastPageLink"
                    currentPageReportTemplate="{first} to {last} of {totalRecords}"
                >
                    <Column field="title" header="" style={{ width: "25%" }}></Column>
                    <Column field="desc" header="" style={{ width: "40%" }}></Column>
                    <Column field="userName" header="" style={{ width: "10%" }}></Column>
                    <Column field="date" header="" style={{ width: "15%" }}></Column>
                    <Column field="" header=" / " body={bodytem} style={{ width: "10%" }}></Column>
                </DataTable>
            </div>
        </>
    );
}

export default Home;
