import React, { useRef, useState } from "react";
import "./post-write.scss";
import $ from "jquery";
import { useCookies } from "react-cookie";
import { Dialog } from "primereact/dialog";
import { Tooltip } from "primereact/tooltip";
import { InputText } from "primereact/inputtext";
import { Editor } from "primereact/editor";
import { FileUpload } from "primereact/fileupload";
import { Button } from "primereact/button";

function Modal({ PWModal, setPWModal, tokenUtill }) {
    const [cookies, setCookie, removeCookie] = useCookies(["reflashToken"]);
    const [EditorText, setEditorText] = useState("");
    const [TitleText, setTitleText] = useState("");

    const fileUploadRef = useRef(null);
    
    function base64toFile(base_data) {
        var arr = base_data.split(","),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]),
            n = bstr.length,
            u8arr = new Uint8Array(n);

        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }
        let fileName = randomString(20)
        
        return new File([u8arr], (fileName + "." +mime.split("/")[1] ), { type: mime });
    }
    const randomString = (length = 8) => {
        const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        let str = '';
        for (let i = 0; i < length; i++) {
          str += chars.charAt(Math.floor(Math.random() * chars.length));
        }
        return str;
      };


    function postWriteSubmit(e) {
        //console.log(e);
        //console.log(TitleText);
        //console.log(EditorText);
        //console.log(fileUploadRef);

        let imgTag = $(EditorText).find("img");
        let newtext = EditorText.toString()
        let formData = new FormData();
        formData.append("operations", JSON.stringify({ query: "mutation ($files: [Upload]!) { fileUpload(files: $files){fileNum fileName fileUrl} }" }));
        let map = {};
        console.log(process.env.PUBLIC_URL)
        for (var i = 0; i < imgTag.length; i++) {
            
            let upfile = base64toFile(imgTag[i].src , ("descfile" +i) )
            newtext = newtext.replace(imgTag[i].src , process.env.PUBLIC_URL +"/ufile/"+upfile.name)
           
            upfile.fileNum = i;
            formData.append(i, upfile);
            map[i] = ["variables.files." + i];
            
        }
        console.log(newtext);
        formData.append("map", JSON.stringify(map))
        
        $.ajax({
            type: "POST",
            url: "http://localhost:3000/graphql",
            data: formData,
            contentType: false,
            processData: false,
            success: function (result) {
                //   
                setEditorText(newtext)
                console.log(result);
            },
            error: function (e) {
                console.log(e);
            },
        });
    }

    const chooseOptions = {
        icon: "pi pi-fw pi-file",
        iconOnly: true,
        className: "custom-choose-btn p-button-rounded p-button-outlined",
    };

    const uploadOptions = {
        className: "custom-none-btn",
    };

    const cancelOptions = {
        icon: "pi pi-fw pi-times",
        iconOnly: true,
        className: "custom-cancel-btn p-button-danger p-button-rounded p-button-outlined",
    };

    return (
        <Dialog
            className="post-write-modal"
            visible={PWModal}
            position={"top"}
            style={{ width: "600px" }}
            onHide={() => setPWModal(false)}
            // footer={footerContent}

            draggable={false}
            resizable={false}
        >  
         {/* <img src={process.env.PUBLIC_URL + '/ufile/BieSYsRHCB9QQKsXEGUS.jpeg' }></img> */}
            <Tooltip target=".custom-choose-btn" content="File Select" position="bottom" />
            <Tooltip target=".custom-cancel-btn" content="Delete All" position="bottom" />

            <div className="post-write-field">
                <InputText placeholder="" value={TitleText} onChange={(e) => setTitleText(e.target.value)} />
                <Editor value={EditorText} onTextChange={(e) => setEditorText(e.htmlValue)} style={{ height: "80%" }} />

                <FileUpload
                    ref={fileUploadRef}
                    chooseOptions={chooseOptions}
                    uploadOptions={uploadOptions}
                    cancelOptions={cancelOptions}
                    multiple
                    accept="*"
                    customUpload
                    // uploadHandler={customBase64Uploader}
                    emptyTemplate={<p className="">     .</p>}
                />
            </div>
            <div className="button-fied">
                <Button type="submit" data-target="LogIn" severity="danger" className="button cancle-button">
                    
                </Button>
                <Button type="submit" data-target="LogIn" className="button submit-button" onClick={postWriteSubmit}>
                    
                </Button>
            </div>
        </Dialog>
    );
}

export default Modal;
